<template>
	<div>
		<!-- wrap -->
		<div id="wrap">
			<!-- header -->
			<header id="sub">
				<div class="header_wrap underLineGray">
					<h1>저장리스트</h1>
				</div>
			</header>
			<!-- header -->
			<!-- content -->
			<div id="save_document">
				<!-- 전체 -->
				<div class="document_list" v-for="(tablet,index) in getTabletList" :key="index">
					<p class="style01"><span>{{tablet.tabletType.codeName}}</span></p>
					<p class="style02"><span>{{tablet.tabletTitle}}</span></p>
					<p class="style03"><span>일반</span><span class="date">{{YYYYMMDDHHmmDate(tablet.regDate)}}</span></p>
					<span class="btn_area">
	          <button class="btn_style01" @click="requestView(tablet)">보기</button>
	          <button class="btn_style02" @click="requestDelete(tablet.id)">삭제</button>
	        </span>
				</div>
				<div class="paging">
					<button v-if="startPage!==1" @click="onPageChange(startPage-1)" class="prev">이전</button>
					<a :class="page===getTabletPage?'on':''" v-for="(page,index) in pages()" :key="index" @click="onPageChange(page)">{{page}}</a>
					<button v-if="getTabletPageTotal>endPage" @click="onPageChange(endPage+1)" class="next">다음</button>
				</div>
			</div>
			<!-- //content -->
			<!-- footer -->
			<Footer/>
			<!-- //footer -->
		</div>

		
		<!-- //wrapper -->
		<div id="prayer_pop">
		<!-- 전광판 관리 팝업-->
		<div class="header_wrap">
			<h1>위패/지방<br>미리보기</h1>
			<button class="btn_close">닫기</button>
			<div class="markWrapper"  ref="printWrapId">
			</div>
		</div>
		</div>

    <!-- //wrapper -->
    <div id="prayer_big_pop">
      <!-- 축문 미리보기 팝업-->
      <div class="header_wrap">
        <h1>위패/지방<br>미리보기</h1>
        <button class="btn_close">닫기</button>
        <div class="markWrapper"  ref="printWrapId2">
        </div>
      </div>
    </div>
		
	</div>
</template>

<script>
import Header from '@/components/common/Header'
import Footer from '@/components/common/Footer'

import myMixin from '@/mixin/mixin'

import { mapGetters } from 'vuex'

export default {
	name: 'TabletList',
	mixins: [myMixin],
	components: {
		Header,
		Footer,
	},
	props: {},
	data() {
		return {
			searchTxt: this.$route.query.searchTxt ? this.$route.query.searchTxt : '',
			tablet: {
				id: null,
				tabletText1: this.$route.query.tabletText1 ? this.$route.query.tabletText1 : '',
				tabletText2: this.$route.query.tabletText2 ? this.$route.query.tabletText2 : '',
				page: {
					page: this.$route.query.page ? this.$route.query.page : 0,
					size: this.$route.query.size ? this.$route.query.size : 5,
					sort: this.$route.query.sort ? this.$route.query.sort : 'regDate,desc',
				}
			},
			pages: function() {
				const list = [];
				for (let i = this.startPage; i <= this.endPage; i += 1) list.push(i);
				return list
			},
		}
	},
	computed: {
		...mapGetters(['getTabletList', 'getTabletPage', 'getTabletPageTotal', 'getTabletTotalElements']),
		startPage() {
			return parseInt((this.getTabletPage - 1) / 5) * 5 + 1
		},
		endPage() {
			return (parseInt((this.getTabletPage - 1) / 5) + 1) * 5 < this.getTabletPageTotal ? (parseInt((this.getTabletPage - 1) / 5) + 1) * 5 : this.getTabletPageTotal
		}
	},
	mounted() {
		this.requestGetTabletList()
	},
	beforeDestroy() {
		this.$store.commit('TABLET_INIT')
	},
	methods: {
		requestDelete(id) {
			this.$store.dispatch('REQUEST_TABLET_DELETE', id)
		},
		requestView(tablet) {
			
			if(tablet.tabletType.codeId === 'TABLET-0'){
				this.$refs.printWrapId.innerHTML = tablet.tabletText
				$('#prayer_pop').addClass('active');
				this.$store.commit('START_DEEMED');
			}else if(tablet.tabletType.codeId === 'TABLET-1' || tablet.tabletType.codeId === 'TABLET-2'){
				this.$refs.printWrapId2.innerHTML = tablet.tabletText
				$('#prayer_big_pop').addClass('active');
				this.$store.commit('START_DEEMED');
			}

			

		},
		requestGetTabletList() {
			const parameter = {
				tablet: this.tablet
			}
			this.$store.dispatch('REQUEST_TABLET_LIST', parameter)
		},
		tabletMore() {
			const param = {
				page: this.tablet.page.page + 1
			}
			this.requestTabletList(param)
		},
		onPageChange(val) {
			const param = {
				page: val
			}
			this.requestTabletList(param)
		},
		requestTabletList(param) {
			let currentPage = 0;
			if (param !== undefined && param !== null &&
				param.page !== undefined && param.page !== null) {
				currentPage = param.page - 1;
			}
			this.$router.push({
				path: '/tabletList',
				query: {
					page: currentPage,
					size: this.tablet.page.size,
					sort: this.tablet.page.sort
				}
			})
		},
	}
}
</script>

